import React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import { Translation } from "react-i18next"
import i18n from "../lib/i18n"

import HeroEsperanza from "../components/heros/hero-esperanza"
import Features from "../components/features/features-home"
import InfinityProducts from "../components/infinity-products"
import TeamEsperanza from "../components/team-esperanza"
import Testimonials from "../components/testimonials"
import FaqEsperanza from "../components/faq/esperanza-faq"
import EsperenzaSpecials from "../components/cta/esperenza-cta"
export const query = graphql`
  query {
    wpHero(id: { eq: "cG9zdDoxMTg3" }) {
      id
      title
      heroFields {
        en {
          headline
          smallcta
          subheadline
          body
          buttontext
          buttonlink
          image {
            id
            sourceUrl
          }
        }
        es {
          headline
          smallcta
          subheadline
          body
          buttontext
          buttonlink
          image {
            id
            sourceUrl
          }
        }
      }
    }
  }
`

const AireEsperanza = ({ data }) => {
  const hero = data.wpHero.heroFields
  return (
    <Layout>
      <Translation>
        {t => (
          <>
            {i18n.language === "en" ? (
              <SEO title="Texas HVAC Company | Residential &amp; Commercial HVAC Contractors" />
            ) : (
              <SEO title="Compañía de HVAC en Texas | Contratadores de HVAC Residencial y Comercial" />
            )}
            <HeroEsperanza
              headline={`${hero[i18n.language].headline}`}
              smallcta={`${hero[i18n.language].smallcta}`}
              subheadline={`${hero[i18n.language].subheadline}`}
              body={`${hero[i18n.language].body}`}
              image={`${hero[i18n.language].image.sourceUrl}`}
              button2Text={`${hero[i18n.language].buttontext}`}
              button2Link={
                hero[i18n.language].buttonlink !== null
                  ? `${hero[i18n.language].buttonlink}`
                  : `#getInTouch`
              }
            />
          </>
        )}
      </Translation>
      <EsperenzaSpecials />
      {/* <InfinityProducts /> */}
      <TeamEsperanza />
      <FaqEsperanza />
      {/* <Testimonials /> */}
    </Layout>
  )
}

export default AireEsperanza
